/**
 * # Offset correction anchor target
 */

@use '../../0-base/1-tools/scss/breakpoint';
@use '../../0-base/0-settings/selector';
@use 'settings/base';

%anchor-target,
.anchor-target {
  &::before {
    content: '';
    display: block;
    width: 0;
    height: base.header__height(default) + base.$anchor__margin;
    margin: -(base.header__height(default) + base.$anchor__margin) 0 0;
    visibility: hidden;

    @include breakpoint.media('≥s') {
      height: base.header__height(s) + base.$anchor__margin;
      margin: -(base.header__height(s) + base.$anchor__margin) 0 0;
    }

    @include breakpoint.media('≥nav-main') {
      height: base.header__height(nav-main) + base.$anchor__margin;
      margin: -(base.header__height(nav-main) + base.$anchor__margin) 0 0;

      #{selector.$header--scrolling} & {
        height: base.header__height(nav-main--scrolling) + base.$anchor__margin;
        margin: -(base.header__height(nav-main--scrolling) + base.$anchor__margin) 0 0;
      }
    }
  }
}
